.cast_container {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  }
  
  .list {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  }
  
  .item {
    list-style: none;
    display: flex;
    flex-direction: column;
  }
  
  .image {
    border: 1px solid lightgrey;
  }
  
  .title {
    font-size: 14px;
    font-weight: bold;
    margin: 0;
    margin-top: 5px;
    max-width: 160px;
  }
  
  .character {
    font-size: 14px;
    font-style: italic;
    margin: 0;
    margin-bottom: 10px;
    max-width: 160px;
  }